.SearchFormInner {
  min-width: 632px;
  position: relative;
  background-color: #fff;
  height: 44px;
  border-radius: 2px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
  -webkit-transition: -webkit-box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: -webkit-box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1);
}

.SearchFormInner:hover {
  -webkit-box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.08);
          box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.08);
}

.SearchFormInner__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 44px;
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  z-index: 1;
  font-size: 16px;
  padding-left: 16px;
  padding-right: 96px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.SearchFormInner__buttons {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
}

.SearchFormInner__button {
  background: none;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
  padding: 0;
  width: 44px;
  height: 44px;
  vertical-align: middle;
  cursor: pointer;
}

.SearchFormInner__button_search {
  color: #4285f4;
  width: 56px;
}

.SearchFormInner__button_speech {
  width: 24px;
  padding-top: 1px;
}

.SearchFormInner__search-icon {
  width: 24px;
}



