.OtherSearchServices__item {
  display: inline-block;
  font-size: 13px;
  color: #666;
}

.OtherSearchServices__item_active {
  border-bottom: 3px solid #4285f4;
  color: #4285f4;
  font-weight: bold;
  padding: 12px 16px;
}

.OtherSearchServices__link {
  color: inherit;
  padding: 12px 16px;
  display: inline-block;
  text-decoration: none;
}

.OtherSearchServices__link:hover {
  color: #222;
}



